<template>
<div class="m_r">
<header class="top_bar">
    <a onclick="window.history.go(-1)" class="icon_back"></a>
    <h3 class="cartname">京东注册</h3>
</header>
<main class="user_login_box">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="用户名" prop="username">
        <el-input v-model.number="ruleForm.username" placeholder="用户名/邮箱/手机号"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="pass">
        <el-input type="password" v-model="ruleForm.pass" autocomplete="off" placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="checkPass">
        <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off" placeholder="请再次输入密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">注册</el-button>
      </el-form-item>
    </el-form>
</main>
</div>
</template>
<script>
export default {
  data () {
    var checkname = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('用户名不能为空'))
      }
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        pass: '',
        checkPass: '',
        username: ''
      },
      rules: {
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        username: [
          { validator: checkname, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      const _this = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          _this.$http.post('/login', {
            loginName: _this.username,
            loginPawd: _this.password
          }).then((res) => {
            // console.log(_this.password)
            if (res.status === 200) {
              _this.userInfo = res.data
              if (_this.userInfo.status === 1) {
                // LOGIN success
                window.sessionStorage.userInfo = JSON.stringify(_this.userInfo)
                // console.log(_this.$store)
                // eslint-disable-next-line no-undef
                _this.$store.dispatch('setUserInfo', userInfo)
                const redirect = decodeURIComponent(_this.$route.query.redirect || '/')
                _this.$router.push({
                  path: redirect
                })
              } else {
                alert(_this.userInfo.msg)
              }
            } else {
              alert('请求出现错误')
            }
            // console.log(res)
          })
        } else {
          // console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

